@import "src/styles/global.scss"; @import "portal/global.scss";



















































@include themeWrap{
    .pl-form-item {
        display: inline-block;
        vertical-align: middle;
        .pl-form-item-label {
            display: inline-block;
            white-space: nowrap;
            font-size: 12px;
            font-weight: bolder;
            color: plVar(colorSubTitle);
        }
        &.pl-form-item-block {
            display: block !important;
        }
    }
}
