@import "src/styles/global.scss"; @import "portal/global.scss";



































































































.pl-tooltip-text {
    display: block;
    height: fit-content;
    outline: none;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    &.pl-tooltip-text-show-overflow-tooltip {
        width: 100%;
    }
}
