@import "src/styles/global.scss"; @import "portal/global.scss";






































@include themeWrap {
    .pl-tag {
        border: solid 1px;
        box-sizing: border-box;
        font-size: 12px;
        border-radius: plVar(borderFillet);
        cursor: pointer;
        user-select: none;
        display: inline-flex;
        align-items: center;

        .pl-tag-close {
            margin-left: 3px;
        }

        @include plColors {
            &.pl-tag-color-#{$key} {
                background-color: $colorLighter;
                border-color: $color;
                color: $colorDeep;

                &:active {
                    background-color: $colorLight;
                }
            }
        }

        @include plSizes {
            &.pl-tag-size-#{$key} {
                height: $value;
                padding: 0 #{$value - 16px};
            }
        }

        &.pl-tag-disabled {
            cursor: not-allowed;
            color: plVar(colorDisabled);
        }
    }
}
