@import "src/styles/global.scss"; @import "portal/global.scss";










































































@include themeWrap {
    .pl-dropdown {
        display: inline-block;
        vertical-align: middle;
        outline: none;

        .pl-dropdown-popper-wrapper {
            display: none;
        }
    }
    .pl-dropdown-popper {
        display: inline-flex;
        flex-direction: column;
        box-sizing: border-box;
        height: 100%;
        width: 100%;
        padding: 3px 3px;

        .pl-dropdown-item {
            .pl-dropdown-item-content {
                padding: 6px 6px;
                font-size: 12px;
                color: plVar(colorContent);
                border-radius: plVar(borderFillet);

                &:hover {
                    cursor: pointer;
                    background-color: plVar(colorPrimaryLighter);
                    color: plVar(colorPrimaryDeep);
                }

                .pl-dropdown-item-icon {
                    margin-right: 6px;
                }
            }

            .pl-dropdown-item-line {
                width: 100%;
                height: 1px;
                background-color: plVar(colorBorder);
                margin: 6px 0;
            }
        }
    }
}
