@import "src/styles/global.scss"; @import "portal/global.scss";







































.pl-base-table-head-item {
    background-color: #efefef;
    overflow: hidden;

    &.pl-base-table-head-item-left, &.pl-base-table-head-item-right {
        position: absolute;
        top: 0;
    }

    &.pl-base-table-head-item-right {
        right: 0;

        & > table {
            float: right;
        }
    }
}
